import AvatarKady from '../assets/images/avatar-kady.jpg';
import AvatarAiysha from '../assets/images/avatar-aiysha.jpg';
import AvatarArthur from '../assets/images/avatar-arthur.jpg';

import BgPattern1 from '../assets/images/bg-pattern-home-4-about-3.svg';
import BgPattern2 from '../assets/images/bg-pattern-home-5.svg'

export function BottomSection() {
    return (
        <section className="bottom-section main-container flex align-center bg3">
            <img src={BgPattern1} className="bg-pattern1" />
            <img src={BgPattern2} className="bg-pattern2" />
            <div className="flex column">
                <h2>Delivering real results for top companies.<br /> Some of our <span>success stories.</span></h2>
                <div class="testemonials flex">
                    <div className="flex column align-center">
                        <p className="fs15">
                            “The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”
                        </p>
                        <p className="flex column">
                            <span className="by fs18">
                                Kady Baker
                            </span>
                            <span className="fs15">
                                Product Manager at Bookmark
                            </span>
                        </p>
                        <img src={AvatarKady} />
                    </div>
                    <div className="flex column align-center">
                        <p className="fs15">
                            “We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”
                        </p>
                        <p className="flex column">
                            <span className="by fs18">
                                Aiysha Reese
                            </span>
                            <span className="fs15">
                                Founder of Manage
                            </span>
                        </p>
                        <img src={AvatarAiysha} />
                    </div>
                    <div className="flex column align-center">
                        <p className="fs15">
                            “Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”
                        </p>
                        <p className="flex column">
                            <span className="by fs18">
                                Arthur Clarke
                            </span>
                            <span className="fs15">
                                Co-founder of MyPhysio
                            </span>
                        </p>
                        <img src={AvatarArthur} />
                    </div>
                </div>
            </div>
        </section>
    )
}